.casinoName {
  font-family: fino-sans,sans-serif;
  font-weight: 600;
  font-style: normal;
  text-align: center;
  font-size: 2em;
  color: rgba(255, 255, 255, 0.781);
  width: 100%;
  padding: 10px 0 0 0;
}

.price {
  font-family: hydrophilia-iced,sans-serif;
  font-weight: 400;
  font-style: normal;
  color: white;
  background-color: rgb(10, 22, 39);
  padding: 10px 0 10px 0;
  margin: 10px 0 10px 0;
  text-align: center;
  border: solid 2px #ffd900;
  width: 97%;
  height: 50px;
  overflow: hidden;
  font-weight: 500;
  font-size: 1.2em;
  border-radius: 5px;
}

.priceInd {
  margin: 0;
  animation: premio .5s forwards linear;
}

.renewButton {
  margin: 20px 0 0 0;
  padding: 20px;
  line-height: 0;
}

@keyframes premio {
  0% {transform: translateY(100px);}
  80% {transform: translateY(-5px);}
  100% {transform: translateY(0);}
}

.slot {
  width: 45vw;
  display: flex;
  flex-direction: row;
  height: 38vw;
  /* justify-content: center; */
  left: 28vw;
  position: absolute;
  overflow: hidden;
  top: 35vw;

}
.fruit_1{
  width: 9vw;
  height: 9vw;
}

.slotFoot {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  padding: 10px;
}

.betInput {
  font-family: hydrophilia-iced,sans-serif;
  font-weight: 400;
  font-style: normal;
  text-align: center;
  background-size: 20px;
  border: none;
  border-radius: 2px;
  font-size: 1.2em;
  margin: 20px;
  height: 40px;
  width: 100%;
  margin: 0 5px 0 0;
  flex: 1 1 auto;
  text-align: center;
}

.spinButton {
  cursor: pointer;
  font-family: hydrophilia-iced,sans-serif;
  font-weight: 400;
  font-style: normal;
  text-align: center;
  font-size: 1.2em;
  margin: 0 0 0 5px;
  flex: 1 1 auto;
  width: 100%;
  height: 40px;
  background-color: #ffd900;
  border: none;
  border-radius: 2px;
}

.buyMoreButton {
  cursor: pointer;
  font-family: hydrophilia-iced,sans-serif;
  font-weight: 400;
  font-style: normal;
  text-align: center;
  font-size: 1em;
  width: calc(100% - 20px);
  height: 30px;
  margin-bottom: 10px;
  background-color: #ffd900;
  border: none;
  border-radius: 2px;; 
}

.row {
  flex: 1 0 auto;
    margin: 0 1px 0 1px;
    overflow: hidden;
    /* border: solid 2px #ffd9009c; */
    background: linear-gradient(0deg, rgb(204, 203, 203) 0%, rgb(255, 255, 255) 20%, rgb(255, 255, 255) 50%, rgb(255, 255, 255) 80%, rgb(204, 203, 203) 100%);}

.ringMoving {
  height: 200px;
  line-height: 200px;
  text-align: center;
  font-size: 2em;
  animation: goaround 0.2s linear infinite;
}

.ringEnd {
  height: 17vw;
  line-height: 0vw;
  text-align: center;
  font-size: 1em;
  animation: stop .5s ease-out forwards;
}
.layer4{
