  top: 66vh;
  left: 26vw;
}
.image4{
  position: absolute;
    height: 100%;
    object-fit: cover
}
@keyframes goaround {
0% {transform: translateY(0);}
100% {transform: translateY(-1000px);}
}

@keyframes stop {
  0% {transform: translateY(0)}
  50% {transform: translateY(-90px)}
  75% {transform: translateY(-80px)}
  100% {transform: translateY(-85px)}
  }
