/* Container for images */
.image-container {
    position: relative; /* Set container to relative so child elements can be positioned inside it */
    width: 100%;       /* Set width for the container */
    height: 100vh;      /* Set height for the container */
  }
  

  
  /* Layer 1 (Background Image) */
  .layer1 {
    z-index: 1; /* This is the lowest layer (background) */
  }
 
  
  /* Layer 3 (Top Image) */
  .layer3 {
    z-index: 2;
    top: 32vh;
    left: 50vw;
  }
